import Store from "./components/Store";   //Store componentStore.js
import Product from "./components/Product";
import Login from "./components/Login";
import Register from "./components/Register";
import Purchase from "./components/Purchase";

import { useState, useEffect, createContext, useContext } from 'react';
import {
  HashRouter as Router,
  Route,
  Routes
} from "react-router-dom";


const AppContext = createContext();

const AppProvider = ({children}) => {
  const [token, setToken] =useState("");                         //token
  const [username, setUsername] = useState("");                  //username
  const [password, setPassword] = useState("");                  //password
  const [error, setError] = useState("");                        //
  const [showing, setShowing] = useState(false);                 //
  const [cartBtnShow, setCartBtnShow] = useState(true);
  const [cartItems, setCartItems] = useState(() => {             //
    const storedCartData = localStorage.getItem("storedCartData");
    return storedCartData ? JSON.parse(storedCartData) : [];    //debug: useEffect()cartItemsuseStateinitialState[]cartItemlocalStorage
  });
  const [purchaseItems, setPurchaseItems] = useState([]);
  const [loading, setLoading] = useState(true);               //Loading spinner

  //
  useEffect(() => {
    const storedToken = localStorage.getItem("userToken");
    if (storedToken) {
      setToken(storedToken);
    }
  },[])


  //
  useEffect(() => {
    localStorage.setItem("storedCartData", JSON.stringify(cartItems));
    // console.log('Saving cartItems to localStorage', cartItems);
  }, [cartItems]);

  const addToCart = (item) => {                             //cartItems Array;
    setCartItems(preItems => [...preItems, item]);
  };


  return (
    <AppContext.Provider value={ {token, setToken, 
                                   username, setUsername, password, setPassword, error, setError,  
                                   showing, setShowing, cartBtnShow, setCartBtnShow, 
                                   cartItems, setCartItems, addToCart,
                                   purchaseItems, setPurchaseItems, 
                                   loading, setLoading} }
    >
      {children}
    </AppContext.Provider>
  );

}

const useAppContext = () => {
  return useContext(AppContext);
}

